<template>
    <v-container>
        <el-upload
                class="upload-demo"
                style="margin: 20px"
                drag
                ref="upload"
                action="/api/issue/post_file"
                multiple
                :file-list="fileList"
                :auto-upload="false"
                v-show="isFile">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text"><em></em></div>
        </el-upload>
        <el-input
                style="margin: 20px"
                type="textarea"
                :rows="2"
                placeholder=""
                autosize
                v-model="textarea"
                v-show="!isFile">
        </el-input>
        <el-button style="margin: 20px" size="small" type="success" @click="changeType"></el-button>

        <el-button style="margin: 20px" size="small" type="success" @click="submitUpload"></el-button>
    </v-container>
</template>

<script>
    import axios from 'axios'
    export default {
        name: "issuePost",
        data() {
            return {
                isFile:true,
                textarea: "",
                fileList: []
            };
        },
        methods: {
            changeType(){
                this.isFile = !this.isFile
            },
            submitUpload() {
                const app = this
                if(app.isFile) {
                    app.$refs.upload.submit();
                    this.$message.success("")
                }else{
                    axios.post('/api/issue/post_txt',{
                        issues:app.textarea
                    }).then(()=>{
                        this.$message.success("")
                        }
                    ).catch(error => {
                        this.$message.error('Err = ' + error.response.data.message)
                    })
                }
            },
        }
    }
</script>

